@charset "UTF-8";
.post-content > * {
  color: var(--aaeditor-color, inherit);
}
.post-content h1, .post-content h2, .post-content h3, .post-content h4, .post-content h5, .post-content h6 {
  position: relative;
  line-height: 26px;
  margin: 18px 0;
  font-size: 18px;
}
.post-content h1:before, .post-content h2:before, .post-content h3:before, .post-content h4:before, .post-content h5:before, .post-content h6:before {
  content: "";
}
.post-content h1 {
  padding: 0 15px 0 15px;
  font-size: 22px;
}
.post-content h1:before {
  font-size: 22px;
  content: "¶";
  position: absolute;
  left: -5px;
  top: 0;
  color: #38bdf8;
}
.post-content h2 {
  padding: 0 15px;
  font-size: 20px;
}
.post-content h2:before {
  position: absolute;
  display: block;
  top: 10%;
  bottom: 10%;
  left: 0;
  width: 4px;
  border-radius: 2px;
  background-color: #38bdf8;
}
.post-content h3 {
  padding: 0 15px;
}
.post-content h3:before {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background-color: #38bdf8;
  border-radius: 0 4px 4px 0;
}
.post-content h4 {
  margin-left: -10px;
}
.post-content h4:before {
  content: "「";
  font-weight: 600;
  margin-right: 5px;
  color: #38bdf8;
}
.post-content h4:after {
  content: "」";
  font-weight: 600;
  margin-left: 5px;
  color: #38bdf8;
}
.post-content h5 {
  padding: 0 15px;
}
.post-content h5::before {
  content: "";
  position: absolute;
  top: 2px;
  left: -8px;
  width: 20px;
  height: 20px;
  background-image: url("data:image/png;base64,");
  background-size: 100% 100%;
}
.post-content h6 {
  padding: 0 15px;
}
.post-content h6::before {
  content: "";
  position: absolute;
  top: 2px;
  left: -8px;
  width: 20px;
  height: 20px;
  background-image: url("data:image/png;base64,");
  background-size: 100% 100%;
}
.post-content hr {
  border-bottom: 2px solid #0ea5e9;
  margin: 16px auto;
}
.post-content p {
  line-height: 24px;
  margin: 0 0 16px;
}
.post-content p:last-child {
  margin-bottom: 0;
}
.post-content blockquote {
  margin: 0 auto 0.5em;
  padding: 12px;
  border: 0;
  background-color: #eff6ff;
  color: #3b82f6;
  line-height: 26px;
  border-radius: 6px;
  position: relative;
  text-indent: 2em;
}
.post-content blockquote:last-child {
  margin-bottom: 0;
}
.post-content blockquote:before {
  content: "“";
  font-size: 4em;
  font-family: monospace;
  position: absolute;
  height: 2em;
  width: 2em;
  left: -0.8em;
  top: 0.4em;
}
.post-content blockquote:last-child {
  margin-bottom: 0;
}
.post-content blockquote p {
  margin: 0;
}
.post-content a:not([class]) {
  line-height: 26px;
  color: #2563eb;
  text-decoration: none;
  transition: all 0.146s;
}
.post-content a:not([class]):hover {
  color: #0284c7;
  text-decoration: underline;
}
.post-content ol {
  list-style: decimal;
  padding-left: 32px;
}
.post-content ol:last-child {
  margin-bottom: 0;
}
.post-content ul {
  list-style: disc;
  padding-left: 32px;
  margin-bottom: 0.5em;
}
.post-content ul:last-child {
  margin-bottom: 0;
}
.post-content table {
  width: 100%;
  font-size: 14px;
  text-align: left;
  color: #64748b;
  border-collapse: separate;
  border-spacing: 0;
  border-radius: var(--aaeditor-table-border-radius, 6px);
  overflow: hidden;
  box-shadow: 0 0 0 1px #e2e8f0;
}
@media (prefers-color-scheme: dark) {
  .post-content table {
    color: #94a3b8;
    box-shadow: 0 0 0 1px #475569;
  }
}
.post-content table td, .post-content table th {
  border: none;
  padding: 12px 16px;
  border-right: 1px solid #e2e8f0;
  border-bottom: 1px solid #e2e8f0;
}
@media (prefers-color-scheme: dark) {
  .post-content table td, .post-content table th {
    border-right-color: #475569;
    border-bottom-color: #475569;
  }
}
.post-content table td:last-child, .post-content table th:last-child {
  border-right: none;
}
.post-content table td[align=center], .post-content table th[align=center] {
  text-align: center;
}
.post-content table thead {
  color: #334155;
  text-transform: uppercase;
  background-color: #f1f5f9;
}
@media (prefers-color-scheme: dark) {
  .post-content table thead {
    color: #f8fafc;
    background-color: #334155;
  }
}
.post-content table thead tr:first-child th {
  border-top: none;
}
.post-content table thead th:first-child {
  border-top-left-radius: 6px;
}
.post-content table thead th:last-child {
  border-top-right-radius: 6px;
}
.post-content table tbody tr {
  background-color: #fff;
}
@media (prefers-color-scheme: dark) {
  .post-content table tbody tr {
    background-color: #1e293b;
  }
}
.post-content table tbody tr:nth-of-type(2n+1) {
  background-color: #f8fafc;
}
@media (prefers-color-scheme: dark) {
  .post-content table tbody tr:nth-of-type(2n+1) {
    background-color: #0f172a;
  }
}
.post-content table tbody tr:last-child td {
  border-bottom: none;
}
.post-content table tbody tr:last-child td:first-child {
  border-bottom-left-radius: 6px;
}
.post-content table tbody tr:last-child td:last-child {
  border-bottom-right-radius: 6px;
}
.post-content table tbody tr th {
  font-weight: 500;
  color: #0f172a;
  white-space: nowrap;
}
@media (prefers-color-scheme: dark) {
  .post-content table tbody tr th {
    color: #f8fafc;
  }
}